import React, { useEffect, useMemo, useState } from 'react';
import {Row, Col, Tab, Nav, InputGroup, FormControl} from 'react-bootstrap'

import Card from '../../components/Card';
import styled from "styled-components";
import SearchIcon from "../../assets/images/search.svg";
import {MarketTable} from "./MarketTable";
import {Link} from "react-router-dom";


const CustomNav = styled(Nav)`
  margin-left: -10px !important;
  margin-right: -10px !important;
`

const CustomNavItem = styled(Nav.Item)`
  padding: 0 10px 10px;
  flex-grow: initial !important;
`


const CustomNavLink = styled(Nav.Link)`
  border-radius: 18px !important;
  color: ${({ theme }) => theme.primary};
  background-color: rgba(71, 183, 254, 0.15);
  padding: 14px 20px;
  min-height: 56px;
  font-weight: 500;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:hover {
    color: ${({ theme }) => theme.primary};
  }
  
  &.active {
    color: white;
    background-color: ${({ theme }) => theme.primary};
  }
`
const PoolsButton = styled.button`
  border-radius: 12px;
  background-color: ${({ theme }) => theme.bg2};
  padding: 6px 20px;
  max-height: 40px;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  font-size: 1rem;
  font-family: inherit;
  font-weight: 500;
  border: none;
  outline: none;
  text-decoration: none;
  
  &:hover,
  &:focus,
  &:active {
      text-decoration: none;
      outline: none;
      box-shadow: none;
  }
`

const TradeButton =  styled(PoolsButton)`
  color: ${({ theme }) => theme.primary};
  
  &:hover {
    color: ${({ theme }) => theme.bg2};
    background-color: ${({ theme }) => theme.primary};
  }
`

const StyledLink = styled(Link)`
  text-decoration: none;
  &:hover,
  &:focus,
  &:active {
      text-decoration: none;
      outline: none;
      box-shadow: none;
  }
`

const Text = styled.span`
  font-size: .875rem;
  font-weight: ${({ fontWeight }) => fontWeight || 700};
  color: ${({ color, theme }) => color ? theme[color] : theme.text1};
`

const MarketCard = props => {
    const [query, setQuery] = useState('');
    const columns = [
        {
            dataField: "pair",
            text: "Pair",
            formatter: (cellContent, row) => {
                return (<Text>{row.pair.join(" / ")}</Text>)
            },
        },
        {
            dataField: "lastPrice",
            text: "Last Price",
            formatter: (cellContent, row) => {
                const price = Number(row.c);
                return (<Text>{price.toFixed(6)}</Text>)
            },
        },
        {
            dataField: "priceChange",
            text: "24h Change",
            formatter: (cellContent, row) => {
                const percent = Number(row.p);

                return (<Text color={percent > 0 ? 'success' : percent < 0 ? 'danger' : 'white'}>{percent > 0 ? '+' : ''}{percent?.toFixed(2)}%</Text>)
            },
        },
        {
            dataField: "priceHigh",
            text: "24h High",
            formatter: (cellContent, row) => {
                const price = Number(row.h);
                return (<Text>{price.toFixed(6)}</Text>)
            },
        },
        {
            dataField: "priceLow",
            text: "24h Low",
            formatter: (cellContent, row) => {
                const price = Number(row.l);
                return (<Text>{price.toFixed(6)}</Text>)
            },
        },
        {
            dataField: "volume",
            text: "24h Volume",
            formatter: (cellContent, row) => {
                return (<Text>{row.q}</Text>)
            },
        },
        {
            dataField: 'edit',
            text: 'Edit',
            formatter: (cellContent, row) => {
                return (
                    <StyledLink to={`/trade/spot/${row.s}`}>
                        <TradeButton>
                            Trade
                        </TradeButton>
                    </StyledLink>
                )
            }
        }
    ]

    const allMarkets = useMemo(() => {

        return Object.values(props.markets).filter(market => !query ? market : JSON.stringify(market).includes(query));
    }, [props.markets, query]);

    const btcMarkets = useMemo(() => {
        return Object.values(props.markets).filter(market => /btc$/.test(market.s.toLowerCase()) && (!query || JSON.stringify(market).includes(query)));
    }, [props.markets, query])

    const ethMarkets = useMemo(() => {
        return Object.values(props.markets).filter(market => /eth$/.test(market.s.toLowerCase()) && (!query || JSON.stringify(market).includes(query)));
    }, [props.markets, query])

    const usdcMarkets = useMemo(() => {
        return Object.values(props.markets).filter(market => /usdc$/.test(market.s.toLowerCase()) && (!query || JSON.stringify(market).includes(query)));
    }, [props.markets, query])

    return (
        <Card>

            <Tab.Container defaultActiveKey={'all'}>
                <Row>
                    <Col xs={12} className={'d-flex align-items-start justify-content-between'} style={{ marginBottom: 25 }}>
                        <CustomNav fill variant="pills" className={'d-flex flex-column flex-lg-row align-items-stretch align-items-lg-center'} >
                            <CustomNavItem>
                                <CustomNavLink eventKey="all">All</CustomNavLink>
                            </CustomNavItem>
                            <CustomNavItem>
                                <CustomNavLink eventKey="btc">BTC Markets</CustomNavLink>
                            </CustomNavItem>
                            <CustomNavItem>
                                <CustomNavLink eventKey="eth">ETH Markets</CustomNavLink>
                            </CustomNavItem>
                            <CustomNavItem>
                                <CustomNavLink eventKey="usdc">USDC Markets</CustomNavLink>
                            </CustomNavItem>
                        </CustomNav>

                        <InputGroup className={'w-auto mb-3 mb-lg-0'}>
                            <InputGroup.Prepend>
                                <InputGroup.Text>
                                    <img src={SearchIcon} alt="search"/>
                                </InputGroup.Text>
                            </InputGroup.Prepend>
                            <FormControl id="PoolsSearch" placeholder="Search" onChange={e => setQuery(e.target.value)}/>
                        </InputGroup>
                    </Col>
                    <Col xs={12}>
                        <Tab.Content className={'bg-transparent'}>
                            <Tab.Pane eventKey="all">
                                <MarketTable columns={columns} entities={allMarkets}/>
                            </Tab.Pane>
                            <Tab.Pane eventKey="btc">
                                <MarketTable columns={columns} entities={btcMarkets}/>
                            </Tab.Pane>
                            <Tab.Pane eventKey="eth">
                                <MarketTable columns={columns} entities={ethMarkets}/>
                            </Tab.Pane>
                            <Tab.Pane eventKey="usdc">
                                <MarketTable columns={columns} entities={usdcMarkets}/>
                            </Tab.Pane>
                        </Tab.Content>
                    </Col>
                </Row>

            </Tab.Container>
        </Card>
    )
}

export default MarketCard;