import React, { useEffect, useState } from 'react';
import { Row, Col } from 'react-bootstrap';
import {useDispatch, useSelector} from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import moment from "moment";
import styled from "styled-components";

import Card from "../../components/Card";
import {fetchProposals, fetchSpaces} from "../../state/governance/actions";
import {shorten} from "../../state/governance/hooks";
import {Link} from "react-router-dom";
import {formatProposals, getScores} from "../../lib/utils";
import {useActiveWeb3React} from "../../hooks";
import Loading from "../../components/Loading";
import Page from "../../components/Page";
import './style.scss';

const Header = styled.div`
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  border: none;
  background-color: transparent;
`

const Title = styled.h2`
  font-size: 1.25rem;
  font-weight: 700;
  color: white;
  margin: 0;
`

const RowTitle = styled.span`
  font-size: 1rem;
  font-weight: 400;
  line-height: 19px;
  color: white;
`

const CellText = styled.span`
  font-weight: 700;
  font-size: .875rem;
  color: white;
  white-space: nowrap;
`

const NewButton = styled(Link)`
    background-color: rgba(71, 183, 254, 0.12);
    height: 56px;
    padding: 6px 20px;
    border-radius: 18px;
    color: ${({ theme }) => theme.primary};
    display: flex;
    align-items: center;
    transition: 0.3s ease all;
    justify-content: center;
   
    :hover,
    :focus, 
    :active{
        background-color: rgba(71, 183, 254, 1);
        color: white;      
        text-decoration: none;
        outline: none;
    }
`

const Proposals = (props) => {
    const { library } = useActiveWeb3React();
    const [selectedProposal, setSelectedProposal] = useState({})
    const dispatch = useDispatch();
    const {spaces, loading: governanceLoading, proposals} = useSelector(state => state.governance);

    let id = props.match.params.space;
    let space = spaces[id];

    useEffect(() => {
        if(Object.keys(spaces).length === 0) {
            dispatch(fetchSpaces());
        } else {
            if(spaces.hasOwnProperty(id)) {
                dispatch(fetchProposals(id));
            } else {
                props.history.push('/tools/governance');
            }
        }
    }, [spaces, id, props.history, dispatch])

    const transformProposals = async (proposals, id) => {
        if(proposals.hasOwnProperty(id)) {
            let result = {};
            const selected = proposals[id];
            const scores = await getScores(
                id,
                space.strategies,
                space.network,
                library,
                Object.values(selected).map((proposal) => proposal.address)
            )
            result = Object.fromEntries(
                Object.entries(selected).map((proposal) => {
                    const transformed = [proposal[0], Object.assign({}, proposal[1])]
                    transformed[1].score = scores.reduce(
                        (a, b) => a + (b[transformed[1].address] || 0),
                        0
                    );
                    return [transformed[0], transformed[1]];
                })
            );

            result = formatProposals(result);
            const transformedProposal = Object.fromEntries(
                Object.entries(result)
                    .sort((a, b) => b[1].msg.payload.end - a[1].msg.payload.end, 0)
            );
            setSelectedProposal(transformedProposal);
        } else {
            setSelectedProposal({})
        }
    }

    useEffect(() => {
        transformProposals(proposals, id)
    }, [proposals, id]);

    const rowEvents = {
        onClick: (e, row) => {
            props.history.push(`/tools/governance/${id}/proposal/${row[0]}`);
        }
    }

    const columns = [
        {
            dataField: "asset",
            text: "Description",
            formatter: (cellContent, row, rowIndex) => (
                <RowTitle>{shorten(row[1].msg.payload.name, 'name')}</RowTitle>
            ),
        },
        {
            dataField: "status",
            text: "status",
            formatter(cellContent, row) {
                const ts = (Date.now() / 1e3).toFixed();
                const { start, end } = row[1].msg.payload;
                let state = ts > end ? {title: 'Closed', className: 'label-light-danger'} : ts > start ? {title: 'Active', className: 'label-light-success'} : {title: 'Pending', className: 'label-light-info'};
                return (
                    <span className={`label ${state.className} label-lg d-flex w-100 label-inline py-3`}>
                        {state.title}
                    </span>
                )
            }
        },
        {
            dataField: "start",
            text: "Start date",
            formatter(cellContent, row) {
                return (
                    <CellText>
                        {moment(row[1].msg.payload.start * 1e3).format('YYYY/MM/DD HH:mm')}
                    </CellText>
                )
            }
        },
        {
            dataField: "end",
            text: "End date",
            formatter(cellContent, row) {
                return (
                    <CellText>
                        {moment(row[1].msg.payload.end * 1e3).format('YYYY/MM/DD HH:mm')}
                    </CellText>
                )
            }
        },
        {
            dataField: "author",
            text: "Author",
            formatter(cellContent, row) {
                return (
                    <CellText>
                        {row[1].address.slice(0, 6)}...{row[1].address.slice(-4)}
                    </CellText>
                )
            }
        },

    ]


    return (
        <Page title={'Governance'}>
            <Row>
                <Col xs={12}>
                    <Card marginTop={-30} marginBottom={0}>
                        <Header>
                            <Title className="card-title">Proposals</Title>
                            <NewButton to={`${props.match.url}/create`} className="bg-light-primary">Create New</NewButton>
                        </Header>
                        <div>
                            {governanceLoading ? (
                                <div className="d-flex align-items-center justify-content-center py-5 w-100">
                                    <Loading color={'primary'} width={40} height={40} active id={'proposals'}/>
                                </div>
                            ) : (
                                <BootstrapTable
                                    wrapperClasses="table-responsive"
                                    bordered={false}
                                    classes={`table table-head-custom table-borderless table-vertical-center overflow-hidden table-hover proposals__table`}
                                    bootstrap4
                                    remote
                                    keyField="id"
                                    columns={columns}
                                    data={Object.entries(selectedProposal)}
                                    rowEvents={rowEvents}
                                >
                                </BootstrapTable>
                            )}
                        </div>
                    </Card>
                </Col>
            </Row>
        </Page>
    )
}

export default Proposals;