import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import SVG from 'react-inlinesvg';
import Skeleton from "react-loading-skeleton";

import {useLogo} from "../../state/governance/hooks";

const Wrapper = styled(Link)`
  border: none;
  background-color: ${({ theme }) => theme.modalBG};
  padding: 50px;
  border-radius: 1.25rem;
  position: relative;
  transition: .3s ease all;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.25rem;
  
  &:hover {
    background-color: ${({ theme, loading }) => !loading && theme.bg2};
    text-decoration: none;
  }
  
  &:focus,
  &:active {
    outline: none;
  }
`

const Logo = styled.img`
  width: 100px;
  height: 100px;
  border-radius: 100px;
  margin-bottom: 1.25rem;
  background-color: white;
`

const Title = styled.h3`
  font-size: 1.25rem;
  font-weight: 700;
  color: ${({ theme }) => theme.text1};
  margin-bottom: 1.125rem;
  text-align: center;
`
const CurrencyName = styled.span`
  font-size: 1rem;
  font-weight: 500;
  color: ${({ theme }) => theme.text1};
  text-align: center;
`

const StarWrapper = styled.div`
  position: absolute;
  right: 20px;
  top: 20px;
  color: ${({ theme }) => theme.primary};
`

const SpaceCard = props => {
    const LogoURL = useLogo(props.id, props.symbolIndex);
    return (
        <Wrapper to={props.loading ? '#' : `/tools/governance/${props.id}`} loading={props.loading}>
            {!props.loading && props.pinned && (
                <StarWrapper>
                    <SVG src={require('../../assets/images/governance/pin-start.svg').default} width={16} height={16} />
                </StarWrapper>
            )}
            {props.loading ? (<Skeleton circle={true} width={100} height={100} style={{ marginBottom: '1.25rem'}}/>) : (<Logo src={LogoURL}/>)}
            <Title>{props.loading ? (<Skeleton height={20} width={130}/>) : props.name}</Title>
            <CurrencyName>{props.loading ? (<Skeleton height={15} width={80}/>) : props.symbol}</CurrencyName>
        </Wrapper>
    )
}

export default SpaceCard;