import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  
`

const Row = styled.div`
  border-radius: 18px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 15px;
  
  &:not(:last-child) {
    margin-bottom: 10px;
  }
`

const Field = styled.div`
  display: flex;
  align-items: center;
  
  &:not(:last-child) {
    margin-bottom: 20px;
  }
`

const Desc = styled.span`
  font-size: .75rem;
  font-weight: 400;
  display: flex;
  min-width: 100px;
`

const Value = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: ${({ direction }) => direction === 'rtl' ? 'flex-end' : 'flex-start'};
  text-align: ${({ direction }) => direction === 'rtl' ? 'right' : 'left'};
`

const ResponsiveTable = ({data, columns, breakpoint, ...props}) => {
    return (
        <Wrapper className={`d-flex d-${breakpoint}-none`}>
            {data.map((row, index) => {
                return (
                    <Row key={`row-${index}`}>
                        {columns.map(col => {
                            return (
                                <Field key={col.dataField}>
                                    <Desc>{col.text}</Desc>
                                    <Value direction={props.direction}>{col.formatter(null, row, index)}</Value>
                                </Field>
                            )
                        })}
                    </Row>
                )
            })}
        </Wrapper>
    )
}

export default ResponsiveTable;